import { Content } from './content';

export class List {
  constructor(container) {
    this.container = container;
    this.oneNoteContent = document.querySelector('#oneNoteContent');
    this.content = new Content(this.oneNoteContent);

    this.noteId = null;
    this.data = [];

    this._init();
  }

  _init() {
    // Ð’Ñ‹Ð±Ð¾Ñ€ Ð¾Ð¿Ñ€ÐµÐ´ÐµÐ»ÐµÐ½Ð½Ð¾Ð¹ Ð·Ð°Ð¼ÐµÑ‚ÐºÐ¸
    this.container.addEventListener(
      'click',
      this._handleChoosenNote.bind(this),
    );
  }

  _handleChoosenNote(e) {
    // Ð£Ð´Ð°Ð»ÐµÐ½Ð¸Ðµ Ñ€Ð°Ð·Ð¼ÐµÑ‚ÐºÐ¸
    this._markUpRemoving(this.container.querySelectorAll('li'));

    const { target } = e;
    this.noteId = this._getIdOfNote(target);
    // Ð”Ð¾Ð±Ð°Ð²Ð»ÐµÐ½Ð¸Ðµ Ñ€Ð°Ð·Ð¼ÐµÑ‚ÐºÐ¸
    this._markUpAddition(this.noteId);

    localStorage.setItem('choosenNoteId', this.noteId); // Ð—Ð°Ð¿Ð¾Ð¼Ð¸Ð½Ð°ÐµÐ¼ id Ð²Ñ‹Ð±Ñ€Ð°Ð½Ð½Ð¾Ð¹ Ð·Ð°Ð¼ÐµÑ‚ÐºÐ¸

    // Ð”Ð¾Ð±Ð°Ð²Ð»ÐµÐ½Ð¸Ðµ Ð¾Ð¿Ð¸ÑÐ°Ð½Ð¸Ñ Ð·Ð°Ð¼ÐµÑ‚ÐºÐ¸
    fetch('http://localhost:8080/api/data', { method: 'GET' })
      .then((response) => response.json())
      .then((data) => {
        data.list.forEach((item) => {
          if (this.noteId == item.id) {
            this.content.render(item, this.render.bind(this));
          }
        });
      })
      .catch((error) => console.error(error));
  }

  // ----------------Ð’ÑÐ¿Ð¾Ð¼Ð¾Ð³Ð°Ñ‚ÐµÐ»ÑŒÐ½Ñ‹Ðµ Ñ„ÑƒÐ½ÐºÑ†Ð¸Ð¸-----------------------

  // Ð£Ð´Ð°Ð»ÐµÐ½Ð¸Ðµ Ñ€Ð°Ð·Ð¼ÐµÑ‚ÐºÐ¸
  _markUpRemoving(noteList) {
    noteList.forEach((item) => {
      item.classList.remove('active');
    });
  }

  // Ð”Ð¾Ð±Ð°Ð²Ð»ÐµÐ½Ð¸Ðµ Ñ€Ð°Ð·Ð¼ÐµÑ‚ÐºÐ¸
  _markUpAddition(noteId) {
    const choosenLi = document.getElementById(noteId);
    choosenLi.classList.add('active');
  }

  // ÐŸÐ¾Ð»ÑƒÑ‡ÐµÐ½Ð¸Ðµ id Ð·Ð°Ð¼ÐµÑ‚ÐºÐ¸
  _getIdOfNote(target) {
    let idOfnote;
    if (!target.hasAttribute('id')) {
      idOfnote = target.parentNode.getAttribute('id');
    } else {
      idOfnote = target.getAttribute('id');
    }
    return idOfnote;
  }

  //--------------------------------------------------------

  render(data) {
    this.data = data;
    this.container.innerHTML = '';

    data.forEach((item) => {
      const template = `<li id=${item.id}>
      <h3>${item.title}</h3>
      <div>${item.time}</div>
      </li>`;

      this.container.innerHTML = this.container.innerHTML + template;
    });

    const activeItemId = Number(localStorage.getItem('choosenNoteId'));
    if (activeItemId) {
      this._markUpAddition(activeItemId);

      fetch('http://localhost:8080/api/data', { method: 'GET' })
        .then((response) => response.json())
        .then((data) => {
          data.list.forEach((item) => {
            if (activeItemId == item.id) {
              this.content.render(item, this.render.bind(this));
            }
          });
        })
        .catch((error) => console.error(error));
    }
  }
}
